import React from "react"
import PropTypes from "prop-types"
import Helmet from "react-helmet"

import "./layout.css"

const Layout = ({ children }) => (
  <>
    <Helmet>
      <link rel="stylesheet" href="fonts.css" />
    </Helmet>
    <main>{children}</main>
  </>
)

Layout.propTypes = {
  children: PropTypes.node.isRequired,
}

export default Layout
